import {UserModel} from "../../model/user";
import { deflate } from 'zlib';
const  userModel = new UserModel()
export default {
  data() {
    return {
        list:[],
        options:{}
    };
  },
  methods: {
      car_check(){
          uni.navigateTo({
              url:'/pagesA/yearly_inspection/yearly_inspection'
          })
      },
      _getCar(){
          userModel.getCars().then((res) => {
            if(res.code < 400) {
                this.list = res.data
            }
          })
      },
      toAddCar(){
          uni.navigateTo({url:'/pagesA/my_add_car/my_add_car'})
      },
      deleteCar(id,index){
          let params = {
            car_id:id
          }
          userModel.deleteCar(params).then((res) => {
              uni.showToast({
                  title:res.message,
                  icon:'none'
              })
              console.log(res)
              if (res.code == 204) {
                  this.list.splice(index,1)
                  console.log('splice')
              }
          })
      },
      choice(item)
      {   
          console.log(this.options.type)
          switch(this.options.type) {
              case 'manual':
              case 'violation_enquiry':
              case 'car_check':
                uni.setStorageSync('choice_car',JSON.stringify(item))
                uni.navigateBack()
              break;
          }
      },
      edit(id)
      {
          console.log(`/pagesA/my_add_car/my_add_car?car_id=` + id)
          uni.navigateTo({
              url:`/pagesA/my_add_car/my_add_car?car_id=` + id
          })
      }
  },
  components: { },
  onLoad: function(options) {
      this.options = options
  },
  onShow: function() {
      this._getCar()
  },
  onHide: function() {}
};
